import { useState } from 'react'
import { Swiper, SwiperSlide } from 'swiper/react'
import { Autoplay, Pagination } from 'swiper/modules'
import 'swiper/css'
import 'swiper/css/pagination'

const Agency = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    budget: '',
    productType: '',
    message: '',
  })

  const handleChange = (e) => {
    const { name, value } = e.target
    setFormData(prev => ({ ...prev, [name]: value }))
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    //   
    console.log(formData)
  }

  const portfolioItems = [
    {
      id: 1,
      title: ' A  ',
      image: '/images/portfolio1.jpg',
      description: '   200%  ',
    },
    {
      id: 2,
      title: ' B  ',
      image: '/images/portfolio2.jpg',
      description: 'TikTok   50  ',
    },
    {
      id: 3,
      title: ' C SNS ',
      image: '/images/portfolio3.jpg',
      description: '6  300% ',
    },
  ]

  return (
    <div className="container-custom py-8">
      {/* Hero Section */}
      <section className="text-center mb-16">
        <h1 className="text-4xl font-bold mb-4"> </h1>
        <p className="text-xl text-gray-600 max-w-2xl mx-auto">
          SNS       
        </p>
      </section>

      {/* Services */}
      <section className="mb-16">
        <h2 className="text-2xl font-bold mb-8"></h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          <div className="border rounded-lg p-6">
            <h3 className="text-xl font-semibold mb-4"> </h3>
            <p className="text-gray-600">
                   
                .
            </p>
          </div>
          <div className="border rounded-lg p-6">
            <h3 className="text-xl font-semibold mb-4">SNS </h3>
            <p className="text-gray-600">
                  SNS   
                 .
            </p>
          </div>
          <div className="border rounded-lg p-6">
            <h3 className="text-xl font-semibold mb-4"> </h3>
            <p className="text-gray-600">
                  
                .
            </p>
          </div>
        </div>
      </section>

      {/* Portfolio */}
      <section className="mb-16">
        <h2 className="text-2xl font-bold mb-8"></h2>
        <Swiper
          modules={[Autoplay, Pagination]}
          spaceBetween={30}
          slidesPerView={1}
          breakpoints={{
            640: {
              slidesPerView: 2,
            },
            1024: {
              slidesPerView: 3,
            },
          }}
          autoplay={{ delay: 5000 }}
          pagination={{ clickable: true }}
        >
          {portfolioItems.map((item) => (
            <SwiperSlide key={item.id}>
              <div className="border rounded-lg overflow-hidden">
                <img
                  src={item.image}
                  alt={item.title}
                  className="w-full h-64 object-cover"
                />
                <div className="p-6">
                  <h3 className="text-xl font-semibold mb-2">{item.title}</h3>
                  <p className="text-gray-600">{item.description}</p>
                </div>
              </div>
            </SwiperSlide>
          ))}
        </Swiper>
      </section>

      {/* Contact Form */}
      <section>
        <h2 className="text-2xl font-bold mb-8"></h2>
        <form onSubmit={handleSubmit} className="max-w-2xl mx-auto">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
            <div>
              <label className="block text-gray-700 mb-2"></label>
              <input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleChange}
                className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                required
              />
            </div>
            <div>
              <label className="block text-gray-700 mb-2"></label>
              <input
                type="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                required
              />
            </div>
            <div>
              <label className="block text-gray-700 mb-2"></label>
              <input
                type="tel"
                name="phone"
                value={formData.phone}
                onChange={handleChange}
                className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                required
              />
            </div>
            <div>
              <label className="block text-gray-700 mb-2"></label>
              <select
                name="budget"
                value={formData.budget}
                onChange={handleChange}
                className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                required
              >
                <option value=""></option>
                <option value="100-300">100-300</option>
                <option value="300-500">300-500</option>
                <option value="500-1000">500-1000</option>
                <option value="1000+">1000 </option>
              </select>
            </div>
          </div>
          <div className="mb-6">
            <label className="block text-gray-700 mb-2"> </label>
            <input
              type="text"
              name="productType"
              value={formData.productType}
              onChange={handleChange}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
              placeholder=": , ,  "
              required
            />
          </div>
          <div className="mb-6">
            <label className="block text-gray-700 mb-2"> </label>
            <textarea
              name="message"
              value={formData.message}
              onChange={handleChange}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary h-32"
              required
            />
          </div>
          <button type="submit" className="btn-primary w-full">
            
          </button>
        </form>
      </section>
    </div>
  )
}

export default Agency 