import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { 
  ShoppingBag, 
  BarChart3, 
  Users, 
  TrendingUp,
  Instagram,
  Film,
  Globe,
  Store
} from 'lucide-react';
import Button from '../components/common/Button';

const SocialMarketing = () => {
  const services = [
    {
      icon: Instagram,
      title: 'SNS  ',
      description: '   , , \n  +   +  '
    },
    {
      icon: Film,
      title: ' ',
      description: '  &   \n    '
    },
    {
      icon: Globe,
      title: '  ',
      description: '      \n    '
    },
    {
      icon: Store,
      title: '  ',
      description: '    \nSNS      '
    }
  ];

  const benefits = [
    {
      icon: ShoppingBag,
      title: '  ',
      description: '     ',
      color: 'bg-[#FF6B6B]'
    },
    {
      icon: BarChart3,
      title: ' +  ',
      description: 'SNS       ',
      color: 'bg-[#4ECDC4]'
    },
    {
      icon: Users,
      title: ' ',
      description: "  ''    ",
      color: 'bg-[#45B7D1]'
    },
    {
      icon: TrendingUp,
      title: '  ',
      description: '    ROI  ',
      color: 'bg-[#96CEB4]'
    }
  ];

  return (
    <div className="min-h-screen bg-white">
      {/* Hero Section */}
      <section className="relative py-20 bg-gradient-to-br from-kong-gold/5 via-white to-white">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8 }}
            className="text-center"
          >
            <h1 className="text-4xl md:text-5xl font-bold text-gray-900 mb-6">
              SNS  +   
            </h1>
            <p className="text-xl md:text-2xl text-gray-600 mb-4">
              " .<br />
                 ."
            </p>
            <p className="text-lg text-gray-600 mb-8 max-w-3xl mx-auto">
                .<br />
              SNS ,  ,  ,     .
            </p>
            <Button size="lg" variant="primary">
               
            </Button>
          </motion.div>
        </div>
      </section>

      {/* Services Section */}
      <section className="py-20 bg-white">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-16">
            <h2 className="text-3xl font-bold text-gray-900 mb-4"> </h2>
            <p className="text-gray-600">   </p>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
            {services.map((service, index) => (
              <motion.div
                key={service.title}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5, delay: index * 0.1 }}
                className="bg-white rounded-2xl p-6 shadow-card hover:shadow-card-hover transition-all"
              >
                <service.icon className="w-12 h-12 text-kong-gold mb-4" />
                <h3 className="text-xl font-semibold text-gray-900 mb-3">{service.title}</h3>
                <p className="text-gray-600 whitespace-pre-line">{service.description}</p>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* Benefits Section */}
      <section className="py-20 bg-gray-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-16">
            <h2 className="text-3xl font-bold text-gray-900 mb-4"> </h2>
            <p className="text-gray-600">   </p>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
            {benefits.map((benefit, index) => (
              <motion.div
                key={benefit.title}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5, delay: index * 0.1 }}
                className="group relative rounded-3xl overflow-hidden shadow-lg hover:shadow-2xl transition-all duration-300"
              >
                <div className="aspect-square relative bg-white p-6">
                  <div className="flex flex-col h-full">
                    <span className={`${benefit.color} p-3 rounded-2xl w-fit`}>
                      <benefit.icon className="w-6 h-6 text-white" />
                    </span>
                    <div className="mt-auto">
                      <h3 className="text-xl font-bold text-gray-900 mb-2 group-hover:translate-x-2 transition-transform duration-300">
                        {benefit.title}
                      </h3>
                      <p className="text-gray-600 text-sm group-hover:translate-x-2 transition-transform duration-300 delay-75">
                        {benefit.description}
                      </p>
                    </div>
                  </div>
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-20 bg-white">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8 }}
          >
            <h2 className="text-3xl font-bold text-gray-900 mb-8">
                
            </h2>
            <Button size="lg" variant="primary">
               
            </Button>
          </motion.div>
        </div>
      </section>
    </div>
  );
};

export default SocialMarketing; 